import React from 'react';
import { render, fireEvent, act } from '@testing-library/react-native';
import ResultsScreen from '../../../src/components/exercise-common/ResultsScreen';
import { Share } from 'react-native';

// Mock des dépendances externes
jest.mock('expo-linear-gradient', () => {
  const { View } = require('react-native');
  return {
    LinearGradient: (props) => <View {...props} />,
  };
});

jest.mock('@expo/vector-icons', () => {
  const { Text } = require('react-native');
  return {
    Ionicons: ({ name }) => <Text testID={`icon-${name}`}>{name}</Text>,
  };
});

describe('ResultsScreen', () => {
  beforeAll(() => {
    jest.useFakeTimers();
  });

  afterAll(() => {
    jest.useRealTimers();
  });

  const mockRetry = jest.fn();
  const mockContinue = jest.fn();

  const baseProps = {
    totalQuestions: 10,
    correctAnswers: 8,
    incorrectAnswers: 1,
    skippedAnswers: 1,
    timeTaken: '01:30',
    exerciseType: 'Vocabulary',
    level: 'B1',
    levelColor: '#3B82F6',
    feedback: '',
    onRetry: mockRetry,
    onContinue: mockContinue,
    showDetailedResults: false,
    detailedResults: [],
  };

  beforeEach(() => {
    jest.clearAllMocks();
    Share.share = jest.fn().mockResolvedValue({});
  });

  it('affiche les statistiques principales', () => {
    const { getByText } = render(<ResultsScreen {...baseProps} />);
    expect(getByText('80%')).toBeTruthy();
    expect(getByText('8/10')).toBeTruthy();
    expect(getByText('Correctes')).toBeTruthy();
    expect(getByText('8')).toBeTruthy();
    expect(getByText('Incorrectes')).toBeTruthy();
    expect(getByText('1')).toBeTruthy();
    expect(getByText('Passées')).toBeTruthy();
    expect(getByText('1')).toBeTruthy();
    expect(getByText('Temps')).toBeTruthy();
    expect(getByText('01:30')).toBeTruthy();
  });

  it('affiche le message de feedback si fourni', () => {
    const props = { ...baseProps, feedback: 'Très bien !' };
    const { getByText } = render(<ResultsScreen {...props} />);
    expect(getByText('Conseils pour progresser')).toBeTruthy();
    expect(getByText('Très bien !')).toBeTruthy();
  });

  it('n\'affiche pas la section de feedback si vide', () => {
    const { queryByText } = render(<ResultsScreen {...baseProps} />);
    expect(queryByText('Conseils pour progresser')).toBeNull();
  });

  it('affiche le détail des réponses si demandé', () => {
    const detailed = [
      { question: 'Quel est le mot ?', userAnswer: 'Chien', correctAnswer: 'Dog', isCorrect: false, isSkipped: false },
    ];
    const props = { ...baseProps, showDetailedResults: true, detailedResults: detailed };
    const { getByText } = render(<ResultsScreen {...props} />);
    expect(getByText('Détail des réponses')).toBeTruthy();
    expect(getByText('Question 1')).toBeTruthy();
    expect(getByText('Quel est le mot ?')).toBeTruthy();
    expect(getByText('Votre réponse:')).toBeTruthy();
    expect(getByText('Chien')).toBeTruthy();
    expect(getByText('Réponse correcte:')).toBeTruthy();
    expect(getByText('Dog')).toBeTruthy();
  });

  it('n\'affiche pas le détail si désactivé', () => {
    const detailed = [
      { question: 'Q', userAnswer: 'A', correctAnswer: 'B', isCorrect: false, isSkipped: false },
    ];
    const props = { ...baseProps, showDetailedResults: false, detailedResults: detailed };
    const { queryByText } = render(<ResultsScreen {...props} />);
    expect(queryByText('Détail des réponses')).toBeNull();
  });

  it('affiche "Excellent!" si score >= 80%', () => {
    const props = { ...baseProps, correctAnswers: 8 };
    const { getByText, getByTestId } = render(<ResultsScreen {...props} />);
    expect(getByText('Excellent!')).toBeTruthy();
    expect(getByTestId('icon-trophy')).toBeTruthy();
  });

  it('affiche "Bien joué!" si score >= 60%', () => {
    const props = { ...baseProps, correctAnswers: 6 };
    const { getByText, getByTestId } = render(<ResultsScreen {...props} />);
    expect(getByText('Bien joué!')).toBeTruthy();
    expect(getByTestId('icon-thumbs-up')).toBeTruthy();
  });

  it('affiche "Pas mal!" si score >= 40%', () => {
    const props = { ...baseProps, correctAnswers: 4 };
    const { getByText, getByTestId } = render(<ResultsScreen {...props} />);
    expect(getByText('Pas mal!')).toBeTruthy();
    expect(getByTestId('icon-fitness')).toBeTruthy();
  });

  it('affiche "Continuez vos efforts" si score < 40%', () => {
    const props = { ...baseProps, correctAnswers: 2 };
    const { getByText, getByTestId } = render(<ResultsScreen {...props} />);
    expect(getByText('Continuez vos efforts')).toBeTruthy();
    expect(getByTestId('icon-school')).toBeTruthy();
  });

  it('déclenche onRetry quand bouton Réessayer est pressé', () => {
    const { getByText } = render(<ResultsScreen {...baseProps} />);
    fireEvent.press(getByText('Réessayer'));
    expect(mockRetry).toHaveBeenCalledTimes(1);
  });

  it('déclenche onContinue quand bouton Continuer est pressé', () => {
    const { getByText } = render(<ResultsScreen {...baseProps} />);
    fireEvent.press(getByText('Continuer'));
    expect(mockContinue).toHaveBeenCalledTimes(1);
  });

  it('déclenche Share.share avec les bonnes données quand bouton Partager est pressé', async () => {
    const { getByText } = render(<ResultsScreen {...baseProps} />);
    await act(async () => {
      fireEvent.press(getByText('Partager'));
    });

    expect(Share.share).toHaveBeenCalledWith({
      message: "J'ai obtenu 8/10 (80%) dans mon exercice de Vocabulary niveau B1 sur l'application JOUD English!",
      title: "Mes résultats d'apprentissage",
    });
  });
});
